import React, {useEffect, useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css'
import axios from "axios";


function MyComponent() {
    const [users, setUsers] = useState([]);

    useEffect(() => {
        axios({
            method: "GET",
            url: 'http://0.0.0.0:8000/api/users/'
        }).then(response => {
            setUsers(response.data)
        })
    }, [])
    return (
        <table>
            <thead>
            <tr>
                <th>ID</th>
                <th>Username</th>
                <th>Email</th>
                <th>Created</th>
                <th>Group</th>
                <th>Actions</th>
            </tr>
            </thead>
            <tbody>
            {users.map(user => (
                <tr key={user.id}>
                    <td>{user.id}</td>
                    <td>{user.username}</td>
                    <td>{user.email}</td>
                    <td>{user.created}</td>
                    <td>{user.group}</td>
                    <td>{user.action}</td>
                </tr>

            ))}
            </tbody>
        </table>
    );

}

export default MyComponent;
